import { getComments } from "@/api/article";
export default {
  name: "ArticleComments",
  components: {},
  props: {
    article: {
      type: Object,
      required: true
    }
  },
  data() {
    return {
      comments: [] //文章列表
    };
  },
  computed: {},
  watch: {},
  created() {},
  async mounted() {
    const { data } = await getComments(this.article.slug);
    this.comments = data.comments;
  },
  methods: {}
};
