export default {
  name: "ArticleMeta",
  components: {},
  props: {
    article: {
      type: Object,
      require: true
    }
  },
  data() {
    return {};
  },
  computed: {},
  watch: {},
  created() {},
  mounted() {},
  methods: {},
  head() {
    return {
      title: `${this.article.title} - RealWorld`,
      meta: [
        {
          hid: "description",
          name: "description",
          content: `${this.article.description}`
        }
      ]
    };
  }
};
